@import "overrides/post.css";

.nav {
  margin-left: 0;
}

/*
Add secondary navigation items in the admin to add Font Awesome icons.
The label is the icon a name (lowercase, case sensitive)
https://fontawesome.com/v4.7.0/icons/
*/

.social-link-fa .fa {
  font-size: 2rem;
  color: white;
  position: relative;
  top: 3px;
}


.site-home-header .site-header-content {
    padding: 5vw 3vw 6vw;
    padding-top: calc(5vw - 55px);
}

/* switch text or icon on social links depending on context */
.site-nav-right .social-link-text {
  display: none;
}
.site-footer-nav .social-link-fa{
  display: none;
}
.site-footer-nav .social-link-text {
  text-transform: capitalize;
}
.site-nav-content .social-link-text {
  color: white;
  opacity: .8;
}
